.grade {
  padding: 24px 20px 34px 20px;
  border-top: 1px solid $line;

  @media (min-width: $tablet-width) {
    padding: 48px 0;
    border: none;
  }

  @media (min-width: $desktop-width) {
    padding-top: 56px;
    padding-bottom: 73px;
  }
}

.grade__line {
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      height: 1px;
      width: 100%;
      background-color: $devider;
      z-index: -1;
    }
  }
}

.grade__wrapper {
  max-width: 400px;
  margin: 0 auto;
}

.grade__title {
  font-size: 18px;
  line-height: 26px;
  color: $caption;
  background-color: $base;
  text-align: center;
  padding: 0;
  margin: 0;
}

.grade__list {
  @include clear-list;

  @media (min-width: $tablet-width) {
    padding: 0 36px;
    display: grid;
    grid-template-columns: 280px 280px;
    gap: 16px;
  }

  @media (min-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    padding: 0 53px;
  }
}

.grade__item {
  margin-bottom: 16px;
  padding-top: 10px;
  padding-bottom: 10px;

  @media (min-width: $tablet-width) {
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    width: 280px;
  }

  &:last-child {
    margin: 0;
  }
}
